import api from "../../../utils/api"

export default {
    namespaced: true,
    state: {
        loggedIn: false,
        user: {}
    },
    mutations: {
        LOGIN(state, data) {
            state.user = data;
            state.loggedIn = true;
            // 
            console.log('');

            api.defaults.headers.common["Token"] = data.token;
        },
        LOGOUT(state) {
            state.user = {};
            state.loggedIn = false;
            if (api.defaults.headers.common["Token"]) {
                api.defaults.headers.common["Token"] = '';
            }
        },

    },
    actions: {
        login({ commit }, data) {
            return api.post('/login', data).then(data => {
                commit("LOGIN", data)
            })
        },
        register({ commit }, data) {
            return api.post('/register', data).then(data => {
                commit("LOGIN", data)

            })
        },
        logout({ commit }) {
            commit('LOGOUT')

        },
        // token,localStoragetokennotification
        check_login({ state }) {
            api.defaults.headers.common["Token"] = state.user.token || '';//
          
        },
    
    },
}